import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import { useState } from "react";
import { useTranslation } from "react-i18next";
import style from "../../FinanceManager/Income/Income.module.css";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import CreateButton from "../../../Ui/Button/CreateButton";
import QvipleLoader from "../../../Loader/QvipleLoader";
import FormInput from "../../../Ui/Input/FormInput";
import { useStudentAdmissionFeeCardMultipleInstallment } from "../../../Hooks/ApiHooks/StudentProfile/student-manager-api";
import {
  existingRequiredField,
  requiredField,
} from "../../../Validation/functions/checkingRequiredField";
import NotifyEvent from "../../../Utils/Snackbar/NotifyEvent";
import FormCalendarInput from "../../../Ui/Input/FormCalendarInput";
import {
  slashDateFormat,
  ymdSlashDateFormat,
} from "../../../Utils/Functions/dateUtility";

const StudentMultipleInstallmentFragment = ({ onClose, pid, install }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    inst_price: "",
    inst_date: "",
  });
  const [errorField, setErrorField] = useState(false);
  const [snackbar, setSnackbar] = useState({
    open: false,
    msg: "",
    variant: "error",
  });

  const [studentAdmissionFeeCardMultipleInstallment] =
    useStudentAdmissionFeeCardMultipleInstallment();

  const onInputChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: +e.target.value,
    }));
  };

  const onInputDateChange = (val) => {
    if (val) {
      setState((prev) => ({
        ...prev,
        inst_date: ymdSlashDateFormat(val),
      }));
    }
  };

  const onUpdate = () => {
    let validation = requiredField(state, []);
    if (existingRequiredField(validation) && pid && install?._id) {
      if (state.inst_price >= install?.remainAmount) {
        setSnackbar({
          msg: "Amount should be less than installment remain amount.",
          open: true,
          variant: "error",
        });
      } else {
        setDisabled((pre) => !pre);
        studentAdmissionFeeCardMultipleInstallment({
          pid: pid,
          installmentFragment: {
            ...state,
            raid: install?._id,
          },
        })
          .then(() => {
            setDisabled((pre) => !pre);
            onClose();
          })
          .catch({});
      }
    } else {
      setErrorField(validation);
    }
  };
  return (
    <>
      <Modal>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("new_installment")}</h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <FormInput
              label={t("installment")}
              placeholder={t("Amount")}
              name="inst_price"
              value={state.inst_price}
              type={"tel"}
              onChange={onInputChange}
              errorShow={errorField["inst_price"]}
            />
            <FormCalendarInput
              label={t("due_last_date")}
              placeholder={t("please_select_the_date")}
              name="inst_date"
              value={state.inst_date ? slashDateFormat(state.inst_date) : ""}
              onChange={onInputDateChange}
              errorShow={errorField["inst_date"]}
            />
            <CreateButton label="create" onAction={onUpdate} />
          </UserMemberContentWrapper>
        </div>
      </Modal>
      {disabled && <QvipleLoader />}
      <NotifyEvent eventState={snackbar} eventStateHandler={setSnackbar} />
    </>
  );
};

export default StudentMultipleInstallmentFragment;
